<template>
  <div id="checkout1"
  :class="[{'has_coupon': this.this_order.coupon.coupon_id !== '無'},
           {'member--loggedin': this.user.id !== ''}]">
    <h1>確認商品數量</h1>
    <div class="checkout1__container">
      <ul class="checkout1__list--m">
        <li class="checkout1__list__item"
        v-for="(item, index) in cart.products"
        :key="index">
          <div class="item__main">
            <div class="item__delete">
              <a href="#" @click.prevent='edit_product(0, item.id)'>
                <img src="@/assets/icon/remove_item.png" alt="">
              </a>
            </div>
            <div class="item__img">
              <a href="#">
                <img :src="item.img" alt="">
              </a>
            </div>
            <div class="item__info">
              <div class="item__text">
                <a href="#">
                  <p class="item__title">{{item.title}}</p>
                  <p class="item__id">{{item.id}}</p>
                  <p class="item__price">NT${{item.price}}</p>
                </a>
              </div>
              <div class="item__quantity">
                <p>數量</p>
                <b-form-spinbutton
                v-model="item.quantity" min="0" max="100"
                @change="edit_product($event, item.id)">
                </b-form-spinbutton>
              </div>
            </div>
          </div>
          <p class="item__delivery">
            黑貓宅急便、專人配送、門市取貨
          </p>
        </li>
      </ul>
      <table class="checkout1__table--l table">
        <thead>
          <tr>
            <th scope="col">品項</th>
            <th scope="col">商品名稱</th>
            <th scope="col">編號</th>
            <th scope="col">單價</th>
            <th scope="col">數量</th>
            <th scope="col">小計</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in cart.products"
          :key="index">
            <td>
              <p class="item__index">{{index + 1}}</p>
            </td>
            <td>
              <div class="item__main">
                <div class="item__delete">
                  <a href="#" @click.prevent='edit_product(0, item.id)'>
                    <img src="@/assets/icon/remove_item.png" alt="">
                  </a>
                </div>
                <a href="#" class="item__link">
                  <div class="item__img">
                    <img :src="item.img" alt="">
                  </div>
                  <p class="item__title">
                    {{item.title}}
                  </p>
                </a>
              </div>
              <p class="item__delivery">黑貓宅急便、專人配送、門市取貨</p>
            </td>
            <td>{{item.id}}</td>
            <td>NT${{item.price}}</td>
            <td>
              <div class="item__quantity">
                <b-form-spinbutton
                v-model="item.quantity"
                min="0" max="100"
                @change="edit_product($event, item.id)"></b-form-spinbutton>
              </div>
            </td>
            <td>NT${{item.price * item.quantity}}</td>
          </tr>
        </tbody>
      </table>
      <div class="checkout1__coupon">
        <div class="coupon__main">
          <span>折扣碼</span>
          <div class="input-group">
            <input type="text" class="form-control" id="coupon_id">
            <div class="input-group-append">
              <button class="btn" type="button"
              @click.prevent="enter_coupon">使用</button>
            </div>
          </div>
        </div>
        <div class="coupon__functionBtns">
          <div class="not_member">
            <a href="#" class="primary">
              加入會員
            </a>
            <a href="#" class="secondary">
              登入查看優惠券
            </a>
          </div>
          <div class="is_member">
            <a href="#"
            class="primary"
            >查詢可使用優惠券</a>
          </div>
          <div class="delete_coupon">
            <a href="#" class="secondary"
            @click.prevent="delete_coupon">取消使用優惠券</a>
          </div>
        </div>
      </div>
      <div class="checkout1__sum">
        <p class="pre_sum">金額小計: NT${{cart.sum}}</p>
        <p class="coupon_id">優惠券碼: {{coupon.coupon_id}}</p>
        <p class="discount">折扣: -NT${{coupon.coupon_discount}}</p>
        <p class="sum">總金額: NT${{sum}}</p>
      </div>
      <p class="checkout1__remark">台灣本島地區全站商品免運配送</p>
      <div class="checkout1__functionBtns">
        <a href="#" class="secondary"
        @click="go_page('front/online_store/all')">繼續購物</a>
        <a href="#" class="primary"
        @click="go_next_step">填寫訂購資料</a>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';

export default {
  watch: {
    $route() {
      this.$bus.$emit('update_step', 1);
    },
  },
  props: ['cart', 'this_order', 'user', 'coupons'],
  data() {
    return {
      coupon: {
        coupon_id: '無',
        coupon_discount_method: '',
        coupon_discount_index: 0,
        coupon_discount: 0,
      },
      sum: 0,
    };
  },
  methods: {
    go_page(pageString) {
      this.$router.push(`/${pageString}`);
    },
    calculate_coupon_discount() {
      switch (this.coupon.coupon_discount_method) {
        case 'minus':
          this.coupon.coupon_discount = this
            .coupon.coupon_discount_index;
          break;
        case 'times':
          this.coupon.coupon_discount = Math.round(this
            .cart.sum * (1 - this.coupon.coupon_discount_index));
          break;
        default:
          this.coupon.coupon_discount = 0;
          break;
      }
      this.sum = this.cart.sum - this.coupon.coupon_discount;
    },
    edit_product(eventNum, id) {
      if (eventNum === 0) {
        this.cart.products.splice(this.cart.products
          .findIndex((item) => item.id === id), 1);
      }
      this.cart.sum = 0;
      this.cart.products.forEach((item) => {
        this.cart.sum += item.price * item.quantity;
      });
      this.calculate_coupon_discount();
      this.$nextTick(() => {
        if (this.cart.products.length === 0) {
          $('#front').removeClass('cart--hasProduct');
        } else {
          $('#front').addClass('cart--hasProduct');
        }
      });
    },
    enter_coupon() {
      if ($('#coupon_id').val() === '') {
        alert('請輸入優惠券碼');
      } else {
        const thisCoupon = this.coupons.find((item) => item.coupon_id === $('#coupon_id').val());
        if (thisCoupon === undefined) {
          alert('優惠券碼輸入錯誤');
        } else {
          this.coupon = JSON.parse(JSON.stringify(thisCoupon));
          this.calculate_coupon_discount();
        }
      }
    },
    delete_coupon() {
      this.coupon = {
        coupon_id: '無',
        coupon_discount_method: '',
        coupon_discount_index: 0,
        coupon_discount: 0,
      };
      this.calculate_coupon_discount();
    },
    go_next_step() {
      if (this.cart.products.length === 0) {
        alert('購物車內無商品，即將跳轉至線上購物頁面');
        this.go_page('front/online_store/all');
      } else {
        this.this_order.id = new Date();
        this.this_order.user.id = this.user.id;
        this.this_order.cart = JSON.parse(JSON.stringify(this.cart));
        this.this_order.coupon = JSON.parse(JSON.stringify(this.coupon));
        this.this_order.sum = JSON.parse(JSON.stringify(this.sum));
        this.go_page('front/checkout/checkout2');
      }
    },
  },
  created() {
    this.this_order.id = '';
    this.this_order.user.id = '';
    this.this_order.cart = {
      is_shown: false,
      sum: 0,
      products: [
        {
          img: '',
          title: '',
          id: '',
          price: 0,
          quantity: 0,
        },
      ],
    };
    this.this_order.coupon = {
      coupon_id: '無',
      coupon_discount_method: '',
      coupon_discount_index: 0,
      coupon_discount: 0,
    };
    this.this_order.sum = 0;
    this.calculate_coupon_discount();
  },
  mounted() {
    this.$bus.$emit('update_step', 1);
  },
};
</script>
